@use 'mixin' as *;

.main_area {
  section {
    margin-bottom: 30px;
    overflow: hidden;
    @include clearfix;
    clear: both;

    h3 {
      font-size: 1.5rem;
      margin-bottom: 20px;
    }

    p {
      margin-bottom: 20px;
    }

    .thumb_img {
      margin: 0 20px 20px 0;
      float: left;
    }

    .left_thumb_para {
      margin-left: 170px;
    }

    .read_more {
      float: right;
      text-align: right;
      padding: 5px 10px;
      border: 1px solid #ccc;
      border-radius: 3px;
      background: #f2f2f2;

      @include LinkArrow('top', #333);
    }
  }
}
